import App from '../App'

const home = r => require.ensure([], () => r(require('../page/home/home')), 'home')
const customer = r => require.ensure([], () => r(require('../page/customer/customer')), 'customer')
const setting = r => require.ensure([], () => r(require('../page/setting/setting')), 'setting')
const help = r => require.ensure([], () => r(require('../page/helpCenter/help')), 'help')
const bindphone = r => require.ensure([], () => r(require('../page/phone/phone')), 'phone')
const vip = r => require.ensure([], () => r(require('../page/vip/vip')), 'vip')
const lottery = r => require.ensure([], () => r(require('../page/lottery/lottery')), 'lottery')
const lotteryBox = r => require.ensure([], () => r(require('../page/lotteryBox/lotteryBox')), 'lotteryBox')
const activity = r => require.ensure([], () => r(require('../page/activity/activity')), 'activity')
const wheel = r => require.ensure([], () => r(require('../page/wheel/wheel')), 'wheel')
const scraping = r => require.ensure([], () => r(require('../page/scraping/scraping')), 'scraping')
const prize = r => require.ensure([], () => r(require('../page/prize/prize')), 'prize')
const activityTemplate = r => require.ensure([], () => r(require('../page/activityTemplate/activityTemplate')), 'activityTemplate')
const awardOff = r => require.ensure([], () => r(require('../page/awardOff/awardOff')), 'awardOff')
const dataStatistics = r => require.ensure([], () => r(require('../page/dataStatistics/dataStatistics')), 'dataStatistics')
const myActivity = r => require.ensure([], () => r(require('../page/myActivity/myActivity')), 'myActivity')
const shareActivity = r => require.ensure([], () => r(require('../page/shareActivity/shareActivity')), 'shareActivity')
const previewActivity = r => require.ensure([], () => r(require('../page/previewActivity/previewActivity')), 'previewActivity')
const baseSetting = r => require.ensure([], () => r(require('../page/activity/baseSetting/baseSetting')), 'baseSetting')
const otherSetting = r => require.ensure([], () => r(require('../page/activity/otherSetting/otherSetting')), 'otherSetting')
const awardSetting = r => require.ensure([], () => r(require('../page/activity/awardSetting/awardSetting')), 'awardSetting')
const seniorSetting = r => require.ensure([], () => r(require('../page/activity/seniorSetting/seniorSetting')), 'seniorSetting')
const customProbability = r => require.ensure([], () => r(require('../page/activity/awardSetting/customProbability/customProbability')), 'customProbability')
const systemProbability = r => require.ensure([], () => r(require('../page/activity/awardSetting/systemProbability/systemProbability')), 'systemProbability')

export default [{
	path:'/',
	component:App,//index.html
	children:[//App.vue
        //home
		{
			path:'',
			meta:{title:''},
			redirect:'/home',
		},
		//
		{
			path:'/home',
			meta:{title:''},
			component:home
		},
		//
		{
			path:'/customer',
			meta:{title:''},
			component:customer
		},
		//
		{
			path:'/setting',
			meta:{title:''},
			component:setting,
			/*children:[
				//
				{
					path:'phone',
					meta:{title:''},
					component:bindphone
				},
			]*/
		},
		//
		{
			path:'phone',
			meta:{title:''},
			component:bindphone
		},
		//
		{
			path:'/help',
			meta:{title:''},
			component:help
		},
		//
		{
			path:'/vip',
			meta:{title:''},
			component:vip
		},
		//
		{
			path:'/lottery',
			meta:{title:''},
			component:lottery
		},
		//
		{
			path:'/lotteryBox',
			meta:{title:''},
			component:lotteryBox
		},
		//
		{
			path:'/activity',
			meta:{title:''},
			component:activity,
			children:[
				{
					path:'',
					component:baseSetting
				},
				{
					path:'baseSetting',
					component:baseSetting
				},
				{
					path:'otherSetting',
					component:otherSetting
				},
				{
					path:'awardSetting',
					component:awardSetting,
					children:[
						{
							path:'',
							component:customProbability
						},
						{
							path:'customProbability',
							component:customProbability
						},
						{
							path:'systemProbability',
							component:systemProbability
						}
					]
				},
				{
					path:'seniorSetting',
					component:seniorSetting
				}
			]
		},
		//
		{
			path:'/wheel',
			meta:{title:''},
			component:wheel
		},
		//
		{
			path:'/scraping',
			meta:{title:''},
			component:scraping
		},
		//
		{
			path:'/prize',
			meta:{title:''},
			component:prize
		},
		//
		{
			path:'/activityTemplate',
			meta:{title:''},
			component:activityTemplate
		},
		//
		{
			path:'/awardOff',
			meta:{title:''},
			component:awardOff
		},
		//
		{
			path:'/dataStatistics',
			meta:{title:''},
			component:dataStatistics
		},
		//
		{
			path:'/myActivity',
			meta:{title:''},
			component:myActivity
		},
		//
		{
			path:'/shareActivity',
			meta:{title:''},
			component:shareActivity
		},
		//
		{
			path:'/previewActivity',
			meta:{title:''},
			component:previewActivity
		}
	]

}]


